import ApiService from "@/core/services/ApiService";
import ISetting from "@/store/modules/setting/interface/setting.interface"; 
import JwtService from "@/core/services/JwtService";
import axios from 'axios';
import {httpOptionsFile, httpOptions} from '../../global';

const apiServie = new ApiService();
const params = {
  headers: httpOptions().headers
};

export default class SettingService{
  getData() {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/setting/layout`, params);
  }

  getDataBackogrund() {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/setting/background`, params);
  }

  getDataConcessionary() {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/setting/concessionary`, params);
  }

  update(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/setting/${id}`, data, httpOptions());
  }

  updateFile(data: any) {
    return axios.post(`${ApiService.vueInstance.axios.defaults.baseURL}/setting/background/file`, data, httpOptionsFile());
  }
}
