
import { defineComponent, onMounted, ref } from "vue";
import SettingService from "@/store/modules/setting/service/setting.service";
import ISetting from "@/store/modules/setting/interface/setting.interface";
import Swal from "sweetalert2/dist/sweetalert2.js";
import { set } from "object-path";

export default defineComponent({
  name: "Background",
  components: {
  },
  setup() {
        const setting = ref<ISetting>({
            id: null,
            description: '',
            value: null,
            slug: '',
        });
        const fileUrl = ref<String>('');
        const settingService = new SettingService();
        const inputAvatar = ref(null);
        let file : any = null;
        const fileObj = ref<Object | null>(null);

        const getData = () => settingService.getDataBackogrund()
        .then((response) => {
            setting.value = response.data
            if(setting.value.value){
              fileUrl.value = setting.value.value
            }
            else{
              fileUrl.value = ''
            }
        })
        .catch((error) => {
            
        });

        const removeImage = () => {
          fileUrl.value = ''
          file = null
          fileObj.value = null
          const a = (document.getElementById('inputAvatar') as HTMLInputElement).value = ''
        }

        const changeFile = (e) => {
          if(e.target.files.length > 0){
            file = e.target.files[0]
            fileObj.value = e.target.files[0]
            fileUrl.value = URL.createObjectURL(file);
          }
        }

        const saveData = () => {
          let formData = new FormData();
          formData.append('id', setting.value.id?setting.value.id.toString():'');
          formData.append('value', file);
          formData.append('description', setting.value.description);
          formData.append('slug', setting.value.slug);
          settingService.updateFile(formData)
          .then((response) => {
              Swal.fire(
              'Confirmaci√≥n!',
              'Informaci√≥n actualizada',
              'success'
              )
              getData()
              removeImage()
          })
          .catch((error) => {
              Swal.fire(
              'Oh no!',
              'No se guardaron los cambios',
              'error'
              )
          });
        };
        
        
        onMounted(() => {
            getData()
        });

        return {
            getData,
            saveData,
            removeImage,
            changeFile,
            inputAvatar,
            setting,
            fileUrl,
            fileObj
        };
  },
});
